<template>
  <div class="hairdresser-start">
    <Loading v-if="isLoading" />
    <div v-else>
      <div class="heading">{{ $t("hairdresserStart.heading") }}</div>
      <div class="queue-number">{{ serving }}</div>
      <Button
        :buttonText="$t('hairdresserStart.button.finish')"
        @click="onFinish"
      />

      <Modal
        :title="$t('hairdresserStart.modal.button.pay')"
        v-if="isShowModal"
        :isShowModal.sync="isShowModal"
        :actionCallback="onConfirm"
        :actionText="$t('hairdresserStart.modal.button.pay')"
      >
        <p>
          {{ $t("hairdresserStart.modal.desc") }}
          <span class="modal-queue-number">{{ serving }}</span>
        </p>
      </Modal>
    </div>
  </div>
</template>

<script>
import Button from "@/components/common/Button";
import Modal from "@/components/common/Modal";
import { PAGE_NAME } from "./const";
import { db } from "@/firebase";
import { mapState } from "vuex";
import { todayDate } from "@/utils";
import { CUSTOMER_STATUS, HAIRDRESSER_STATUS } from "@/const";

export default {
  name: "HairdresserStart",
  components: {
    Button,
    Modal,
  },
  props: {
    setPageName: {
      type: Function,
      default: () => {},
    },
  },
  data() {
    return {
      isShowModal: false,
      serving: null,
      isLoading: false,
    };
  },
  computed: {
    ...mapState(["user"]),
  },
  methods: {
    async getServingNumber() {
      const hairdresser = await db
        .collection("hairdresser")
        .doc(this.user.uid)
        .get();
      this.serving = hairdresser.data().serving;
    },
    onFinish() {
      this.isShowModal = true;
    },
    onConfirm() {
      this.isLoading = true;
      const customerUpdate = db
        .collection("customer")
        .doc(todayDate)
        .update({
          [`customers.${this.serving}.status`]: CUSTOMER_STATUS.FINISHED,
        });
      const hairdresserUpdate = db
        .collection("hairdresser")
        .doc(this.user.uid)
        .update({
          status: HAIRDRESSER_STATUS.FINISHED,
        });
      Promise.all([customerUpdate, hairdresserUpdate])
        .then(() => {
          this.setPageName(PAGE_NAME.PAYMENT);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  },
  created() {
    this.getServingNumber();
  },
};
</script>

<style lang="scss" scoped>
.hairdresser-start {
  text-align: center;

  .heading {
    font-size: 26px;
  }

  .queue-number {
    color: $primary;
    font-size: 80px;
    margin: 24px 0 40px 0;
  }

  .modal-queue-number {
    color: $primary;
    font-size: 32px;
    margin: 0 8px;
  }
}
</style>
