<template>
  <div class="hairdresser-next">
    <Loading v-if="isLoading" />
    <div v-else>
      <div class="heading">{{ $t("hairdresserNext.heading") }}</div>
      <div class="queue-number">{{ $t(nextCustomerNumber) }}</div>
      <div class="button-wrapper">
        <Button
          :disabled="!hasCustomers"
          :buttonText="$t('hairdresserNext.button.proceed')"
          @click="onAction(hairdresserStatus.CUTTING)"
        />
        <Button
          :disabled="!hasCustomers"
          :buttonText="$t('hairdresserNext.button.skip')"
          :buttonCancel="!hasCustomers"
          @click="onAction(hairdresserStatus.SKIPPED)"
        />
      </div>
      <Modal
        :title="$t(modalTitle[actionType])"
        v-if="isShowModal"
        :isShowModal.sync="isShowModal"
        :actionCallback="onConfirm"
        :actionText="$t(actionText[actionType])"
      >
        <template v-if="actionType === hairdresserStatus.CUTTING">
          <p>
            {{ $t("hairdresserNext.modal.proceed.desc1") }}
          </p>
          <p>
            {{ $t("hairdresserNext.modal.proceed.desc2") }}
            <span class="modal-queue-number">{{ nextCustomerNumber }}</span>
          </p>
        </template>
        <template v-else>
          <p>
            {{ $t("hairdresserNext.modal.skip.desc") }}
            <span class="modal-queue-number">{{ nextCustomerNumber }}</span>
          </p>
        </template>
      </Modal>
    </div>
  </div>
</template>

<script>
import Button from "@/components/common/Button";
import Modal from "@/components/common/Modal";
import { todayDate } from "@/utils";
import { PAGE_NAME } from "./const";
import { mapState } from "vuex";
import { db } from "@/firebase";
import { HAIRDRESSER_STATUS, CUSTOMER_STATUS } from "@/const";

export default {
  name: "HairdresserNext",
  components: {
    Button,
    Modal,
  },
  props: {
    setPageName: {
      type: Function,
      default: () => {},
    },
    nextCustomerData: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      isShowModal: false,
      actionType: null,
      waitingCustomers: [],
      hairdresserStatus: HAIRDRESSER_STATUS,
      isLoading: false,
    };
  },
  computed: {
    ...mapState(["user"]),
    modalTitle() {
      return {
        [HAIRDRESSER_STATUS.CUTTING]: "hairdresserNext.modal.title1",
        [HAIRDRESSER_STATUS.SKIPPED]: "hairdresserNext.modal.title2",
      };
    },
    actionText() {
      return {
        [HAIRDRESSER_STATUS.CUTTING]: "hairdresserNext.button.proceed",
        [HAIRDRESSER_STATUS.SKIPPED]: "hairdresserNext.button.skip",
      };
    },
    nextCustomerNumber() {
      return this.hasCustomers
        ? this.nextCustomerData.number
        : "hairdresserNext.noCustomer";
    },
    hasCustomers() {
      return this.nextCustomerData !== null;
    },
  },
  methods: {
    onAction(type) {
      this.actionType = type;
      this.isShowModal = true;
    },
    onConfirm() {
      this.isLoading = true;
      if (this.actionType === HAIRDRESSER_STATUS.CUTTING) {
        const hairdresserUpdate = db
          .collection("hairdresser")
          .doc(this.user.uid)
          .update({
            status: HAIRDRESSER_STATUS.CUTTING,
            serving: this.nextCustomerNumber,
          });

        const customerUpdate = db
          .collection("customer")
          .doc(todayDate)
          .update({
            [`customers.${this.nextCustomerNumber}.status`]:
              CUSTOMER_STATUS.CUTTING,
          });

        Promise.all([hairdresserUpdate, customerUpdate])
          .then(() => {
            this.setPageName(PAGE_NAME.START);
          })
          .finally(() => {
            this.isLoading = false;
          });
        return;
      }
      // skip customer
      const customerRef = db.collection("customer").doc(todayDate);
      customerRef.update({
        [`customers.${this.nextCustomerNumber}.status`]:
          CUSTOMER_STATUS.SKIPPED,
      });
      this.isLoading = false;
      this.isShowModal = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.hairdresser-next {
  text-align: center;

  .heading {
    font-size: 26px;
  }

  .queue-number {
    color: $primary;
    font-size: 80px;
    margin: 24px 0 40px 0;
  }

  .button-wrapper {
    .button:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  .modal-queue-number {
    color: $primary;
    font-size: 32px;
    margin: 0 8px;
  }
}
</style>
