<template>
  <div class="confirm-page">
    <div class="title">{{ $t("confirm.title") }}</div>
    <div class="text-number" ref="number">{{ showNumber }}</div>
    <div class="date-time">{{ showDate }}</div>
    <Button
      class="button"
      :buttonText="$t('common.backHome')"
      buttonCancel
      @click="onHome"
    />
  </div>
</template>

<script>
import Button from "@/components/common/Button";
import { formatDate } from "@/utils";

export default {
  name: "ConfirmPage",
  components: {
    Button,
  },
  props: {
    customer: {
      type: Object,
      default: () => {},
    },
  },
  computed: {
    showNumber() {
      return Array.isArray(this.customer.number)
        ? this.customer.number.join(", ")
        : this.customer.number;
    },
    showDate() {
      return formatDate(this.customer.createdAt);
    },
  },
  methods: {
    onHome() {
      this.$router.replace({
        name: "home",
      });
    },
  },
  created() {
    setTimeout(() => {
      this.onHome();
    }, 3000);
  },
  beforeRouteEnter(to, from, next) {
    if (["search", "onlineReservation"].includes(from.name)) {
      next();
      return;
    }
    next({ name: "search" });
  },
};
</script>

