<template>
  <div class="modal">
    <div class="modal-wrapper">
      <div class="header">
        <div>{{ title }}</div>
        <IconClose class="icon-close" @click.native="onClose" />
      </div>
      <hr />
      <div class="content">
        <slot></slot>
        <div class="modal-button-wrapper">
          <Button :buttonText="actionText" @click="actionCallback" />
          <Button
            buttonCancel
            :buttonText="$t('common.cancel')"
            @click="onClose"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import IconClose from "@/components/common/icons/IconClose";
import Button from "@/components/common/Button";

export default {
  name: "Modal",
  props: {
    title: {
      type: String,
      default: "Title",
    },
    isShowModal: {
      type: Boolean,
      default: false,
    },
    actionText: {
      type: String,
      default: "Confirm",
    },
    actionCallback: {
      type: Function,
      default: () => {},
    },
  },
  components: {
    IconClose,
    Button,
  },
  methods: {
    onClose() {
      this.$emit("update:isShowModal", false);
    },
  },
};
</script>

