import Vue from "vue";
import Router from "vue-router";
import Home from "@/components/Home";
import OnlineReservation from "@/components/OnlineReservation";
import ConfirmPage from "@/components/ConfirmPage";
import SearchPage from "@/components/SearchPage";
import Hairdresser from "@/components/hairdresser/Hairdresser";
import Login from "@/components/Login";

Vue.use(Router);

const router = new Router({
  mode: "hash",
  base: "/",
  routes: [
    {
      path: "/",
      name: "home",
      component: Home,
    },
    {
      path: "/login",
      name: "login",
      component: Login,
    },
    {
      path: "/online-reservation",
      name: "onlineReservation",
      component: OnlineReservation,
      meta: {
        admin: true,
      },
    },
    {
      path: "/confirm",
      name: "confirm",
      component: ConfirmPage,
      props: true,
    },
    {
      path: "/search",
      name: "search",
      component: SearchPage,
    },
    {
      path: "/hairdresser",
      name: "hairdresser",
      component: Hairdresser,
      meta: {
        requiresAuth: true,
      },
    },
    {
      path: "*",
      redirect: { name: "home" },
    },
  ],
});

export default router;
