<template>
  <div class="search-page">
    <Loading v-if="isLoading" />
    <div v-else>
      <Input
        :label="$t('search.label.phoneNumber')"
        :placeholder="$t('search.placeholder.phoneNumber')"
        type="tel"
        inputmode="numeric"
        v-model="phoneNumber"
      />
      <div class="button-wrapper">
        <Button
          class="button-search"
          @click="onSearch"
          :buttonText="$t('common.search')"
          :disabled="isButtonDisabled"
        />
        <Button
          class="button-back"
          @click="onBack"
          :buttonText="$t('common.back')"
          buttonCancel
        />
      </div>
      <div class="error" v-if="error">
        {{ $t("search.error.phoneNumberNotExist") }}
      </div>
    </div>
  </div>
</template>

<script>
import Input from "@/components/common/Input";
import Button from "@/components/common/Button";
import { db } from "@/firebase";
import { todayDate, isPhoneNumber } from "@/utils";

export default {
  name: "SearchPage",
  components: {
    Input,
    Button,
  },
  data() {
    return {
      phoneNumber: "",
      error: false,
      isLoading: false,
    };
  },
  computed: {
    isButtonDisabled() {
      return !isPhoneNumber(this.phoneNumber);
    },
  },
  methods: {
    async getCustomerByPhone(phoneNumber) {
      const customersDoc = await db.collection("customer").doc(todayDate).get();
      const customers = customersDoc.data().customers;
      return Object.values(customers).filter((customer) => {
        return customer.phoneNumber === phoneNumber;
      });
    },
    async onSearch() {
      this.isLoading = true;
      const searchCustomer = await this.getCustomerByPhone(this.phoneNumber);
      if (!searchCustomer.length) {
        this.isLoading = false;
        this.error = true;
        return;
      }

      const mappedCustomer = {
        number: [],
        createdAt: searchCustomer[searchCustomer.length - 1].createdAt,
        phoneNumber: searchCustomer[0].phoneNumber,
      };
      searchCustomer.forEach((customer) => {
        mappedCustomer["number"].push(customer.number);
      });

      this.$router.push({
        name: "confirm",
        params: { customer: mappedCustomer },
      });
    },
    onBack() {
      this.$router.replace({
        name: "home",
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.search-page {
  max-width: 600px;
  margin: 0 auto;

  .button-wrapper {
    margin-top: 40px;
  }

  .button-back {
    margin-top: 24px;
  }

  .error {
    text-align: center;
    margin-top: 24px;
    color: $primary;
    font-size: 22px;
  }
}
</style>
