<template>
  <div id="app">
    <div v-if="!isLoading">
      <div class="btn-logout" @click="onLogout" v-if="isUserAuth">Logout</div>
      <div class="app-wrapper">
        <img class="logo-image" :src="imgSrc" alt="EZ CUT LOGO" />
        <router-view></router-view>
      </div>
      <LanguageSwitcher class="language" />
    </div>
  </div>
</template>

<script>
import Logo from "@/assets/img/logo.png";
import LanguageSwitcher from "@/components/common/LanguageSwitcher";
import { mapActions, mapGetters } from "vuex";
import { firebase } from "@/firebase";
import store from "@/store";
import { TYPES } from "@/store/mutations";
import { getIsAdmin } from "@/firebaseFunctions";
import { db } from "@/firebase";
import { todayDate } from "@/utils";
import { connectPrinter } from "@/printUtils";

export default {
  name: "App",
  components: {
    LanguageSwitcher,
  },
  data() {
    return {
      imgSrc: Logo,
      isLoading: false,
      waitingNumber: 0,
    };
  },
  computed: {
    ...mapGetters(["isUserAuth"]),
  },
  created() {
    this.init();
    this.addFirebaseLoginEvent();
  },
  methods: {
    ...mapActions(["logoutAction"]),
    async init() {
      this.isLoading = true;
      // get today's data (# of people waiting)
      const customerRef = db.collection("customer").doc(todayDate);
      const customerSnapshot = await customerRef.get();

      // check if document exist
      if (customerSnapshot.exists) {
        customerRef.update({ waiting: this.waitingNumber }).then(() => {
          this.isLoading = false;
        });
      } else {
        // if doc does not exist create a new one
        customerRef
          .set({ customers: {}, latestNumber: 0, waiting: 0 })
          .then(() => {
            this.isLoading = false;
          }); // create the document
      }
    },
    addFirebaseLoginEvent() {
      this.isLoading = true;
      firebase.auth().onAuthStateChanged((userAuth) => {
        // if user not authenticated and to route meta data auth is required
        // redirect to login page
        if (!userAuth && !!this.$router?.meta?.requiresAuth) {
          this.$router.push({
            name: "login",
          });
          this.isLoading = false;
        } else if (userAuth) {
          store.commit(TYPES.SET_USER, userAuth);
          getIsAdmin(userAuth.uid).then((isAdmin) => {
            if (isAdmin) {
              store.commit(TYPES.SET_ROLE, "Admin");
              connectPrinter();
            }
            this.isLoading = false;
          });
        } else {
          this.isLoading = false;
        }
      });
    },
    async onLogout() {
      await this.logoutAction();
      this.$router.replace({
        name: "home",
      });
    },
  },
};
</script>

