<script>
export default {
  name: "Icon",

  functional: true,

  props: {
    width: {
      type: [Number, String],
      default: 24,
    },

    height: {
      type: [Number, String],
      default: 24,
    },

    viewBox: {
      type: String,
      required: false,
    },
  },

  render(createElement, context) {
    const { props, children, listeners } = context;
    const data = { ...context.data };
    data.class = "svg-icon";
    data.attrs = {
      xmlns: "http://www.w3.org/2000/svg",
      width: props.width,
      height: props.height,
      viewBox: props.viewBox || `0 0 ${props.width} ${props.height}`,
      role: "presentation",
      fill: "currentColor",
      "fill-rule": "nonzero",
      stroke: "none",
      "stroke-width": "1",
      "stroke-linecap": "butt",
      "stroke-linejoin": "miter",
      ...data.attrs,
    };
    data.on = listeners;

    return createElement("svg", data, children);
  },
};
</script>

<style lang="scss" scoped>
.svg-icon {
  display: inline-block;
  vertical-align: baseline;
}
</style>
