<template>
  <div class="hairdresser-payment">
    <Loading v-if="isLoading" />
    <div v-else>
      <div class="heading">{{ $t("hairdresserPayment.heading") }}</div>
      <div class="payment-wrapper">
        <div class="icon-wrapper" @click="onPay(paymentType.CASH)">
          <IconCash class="icon" />
          <div>{{ $t("hairdresserPayment.payment.cash") }}</div>
        </div>
        <div class="icon-wrapper" @click="onPay(paymentType.LINE)">
          <IconLine class="icon" />
          <div>{{ $t("hairdresserPayment.payment.line") }}</div>
        </div>
        <div class="icon-wrapper" @click="onPay(paymentType.JKOPAY)">
          <IconJK class="icon" />
          <div>{{ $t("hairdresserPayment.payment.jkopay") }}</div>
        </div>
      </div>

      <Modal
        :title="$t(modalTitle[paymentSelected])"
        v-if="isShowModal"
        :isShowModal.sync="isShowModal"
        :actionCallback="onConfirm"
        :actionText="$t('common.confirm')"
      >
        <p v-if="paymentSelected === 'cash'">
          {{ $t("hairdresserPayment.cash.desc") }}
        </p>
        <component class="img-comp" :is="imgComp[paymentSelected]" />
      </Modal>
    </div>
  </div>
</template>

<script>
import IconLine from "@/components/common/icons/IconLine";
import IconCash from "@/components/common/icons/IconCash";
import IconJK from "@/components/common/icons/IconJK";
import IconJKQR from "@/components/common/icons/IconJKQR";
import IconLineQR from "@/components/common/icons/IconLineQR";
import Modal from "@/components/common/Modal";
import { PAGE_NAME } from "./const";
import { HAIRDRESSER_STATUS } from "@/const";
import { db } from "@/firebase";
import { mapState } from "vuex";

const PAYMENT_TYPE = {
  LINE: "line",
  JKOPAY: "jkopay",
  CASH: "cash",
};

export default {
  name: "HairdresserPayment",
  components: {
    IconLine,
    IconCash,
    IconJK,
    IconJKQR,
    IconLineQR,
    Modal,
  },
  props: {
    setPageName: {
      type: Function,
      default: () => {},
    },
  },
  data() {
    return {
      paymentType: PAYMENT_TYPE,
      isShowModal: false,
      paymentSelected: null,
      serving: null,
      isLoading: false,
    };
  },
  computed: {
    ...mapState(["user"]),
    modalTitle() {
      return {
        [PAYMENT_TYPE.LINE]: "hairdresserPayment.payment.line",
        [PAYMENT_TYPE.CASH]: "hairdresserPayment.payment.cash",
        [PAYMENT_TYPE.JKOPAY]: "hairdresserPayment.payment.jkopay",
      };
    },
    imgComp() {
      return {
        [PAYMENT_TYPE.LINE]: IconLineQR,
        [PAYMENT_TYPE.JKOPAY]: IconJKQR,
      };
    },
  },
  methods: {
    onPay(paymentType) {
      this.paymentSelected = paymentType;
      this.isShowModal = true;
    },
    onConfirm() {
      this.isLoading = true;
      // TODO: save data and return to step 1
      const hairdresserRef = db.collection("hairdresser").doc(this.user.uid);
      hairdresserRef
        .update({
          status: HAIRDRESSER_STATUS.WAITING,
          serving: null,
        })
        .then(() => {
          this.setPageName(PAGE_NAME.NEXT);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
  },
};
</script>

