.hairdresser-payment {
  .heading {
    font-size: 26px;
  }

  .icon {
    width: 80px;
    height: 80px;
    margin-right: 24px;
  }

  .payment-wrapper {
    display: flex;
    flex-direction: column;
    margin: 24px 0 40px 0;
  }

  .icon-wrapper {
    display: flex;
    align-items: center;
    cursor: pointer;

    &:not(:last-child) {
      margin-bottom: 24px;
    }
  }

  .img-comp {
    width: 75%;
    display: block;
    margin: 0 auto;
  }
}
</style>
