<template>
  <div class="online-reservation">
    <img
      ref="imgRef"
      id="myImg"
      src="../assets/img/print_logo.png"
      alt=""
      style="display: none"
    />
    <canvas
      ref="canvasRef"
      id="canvas"
      width="272"
      height="300"
      style="display: none"
    />
    <Loading v-if="isLoading" />
    <div v-else>
      <!-- TODO: temp remove -->
      <!-- <div class="button-group">
        <div class="title">是否指定</div>
        <div class="select-wrapper">
          <div
            class="button-select button-assigned"
            :class="{ 'number-selected': assignedStatus === index }"
            v-for="(select, index) in ['不指定', '指定']"
            :key="index"
            @click="onAssigned(index)"
          >
            {{ select }}
          </div>
        </div>
      </div> -->
      <div class="hairdresser-wrapper" v-if="assignedStatus === 1">
        <div class="button-group">
          <div class="title">請選擇設計師</div>
          <div class="select-wrapper">
            <div
              class="button-select button-hairdresser"
              :class="{
                'number-selected':
                  form.selectedHairdresser.id === hairdresser.id,
              }"
              v-for="hairdresser in hairdressers"
              :key="hairdresser.id"
              @click="onHairdresserSelected(hairdresser)"
            >
              {{ hairdresser.name }}
            </div>
          </div>
        </div>
      </div>
      <Input
        class="input-number"
        :label="$t('reserve.phoneNumber.input.title')"
        type="tel"
        inputmode="numeric"
        :placeholder="$t('reserve.phoneNumber.input.placeholder')"
        v-model="form.phoneNumber"
        v-if="role !== 'Admin'"
      />
      <div class="button-wrapper">
        <Button
          class="button-confirm"
          :buttonText="$t('common.confirm')"
          :disabled="isButtonDisabled"
          @click="onConfirm"
        />
        <Button
          class="button-back"
          :buttonText="$t('common.back')"
          buttonCancel
          @click="onBack"
        />
      </div>
    </div>
  </div>
</template>

<script>
import Input from "@/components/common/Input";
import Button from "@/components/common/Button";
import { db } from "@/firebase";
import { todayDate, isPhoneNumber } from "@/utils";
import { mapState, mapGetters } from "vuex";
import { getLatestNumber } from "@/firebaseFunctions";
import { CUSTOMER_STATUS } from "@/const";
import { onPrint } from "@/printUtils";

const ASSIGNED_STATUS = {
  UNASSIGNED: 0,
  ASSIGNED: 1,
};

export default {
  name: "OnlineReservation",
  components: {
    Input,
    Button,
  },
  data() {
    return {
      form: {
        phoneNumber: "",
        selectedHairdresser: null,
      },
      hairdressers: [],
      assignedStatus: 0,
      isLoading: false,
      imgContext: null,
    };
  },
  computed: {
    ...mapGetters(["isAdminAuth"]),
    ...mapState(["role"]),
    isButtonDisabled() {
      return !isPhoneNumber(this.form.phoneNumber) && this.role !== "Admin";
    },
  },
  methods: {
    onAssigned(index = ASSIGNED_STATUS.UNASSIGNED) {
      this.assignedStatus = index;
      if (index === ASSIGNED_STATUS.UNASSIGNED) {
        this.form.selectedHairdresser = null;
        return;
      }
      this.form.selectedHairdresser = this.hairdressers[0];
    },
    onHairdresserSelected(hairdresser) {
      this.form.selectedHairdresser = hairdresser;
    },
    async getHairdresser() {
      this.isLoading = true;
      const haridresserDocs = await db.collection("hairdresser").get();
      haridresserDocs.forEach((doc) => {
        this.hairdressers = [
          ...this.hairdressers,
          { id: doc.id, name: doc.data().name },
        ];
      });
      this.isLoading = false;
    },
    async onConfirm() {
      // setup ref to db
      const customerRef = db.collection("customer").doc(todayDate);

      // save to DB
      const latestNumber = await getLatestNumber();
      // initialize customer data
      const data = {
        createdAt: Date.now(),
        number: latestNumber + 1,
        phoneNumber: this.form.phoneNumber,
        assignedHairdresser: this.form.selectedHairdresser,
        status: CUSTOMER_STATUS.WAITING,
      };

      onPrint(data, this.imgContext)
        .then(() => {
          // add to array of customers in firestore
          customerRef.set(
            {
              customers: {
                [latestNumber + 1]: data,
              },
              latestNumber: latestNumber + 1,
            },
            { merge: true }
          );

          // go to confirm page
          this.$router.push({
            name: "confirm",
            params: { customer: data },
          });
        })
        .catch((e) => {
          alert(e);
        });
    },
    onBack() {
      this.$router.replace({
        name: "home",
      });
    },
  },
  mounted() {
    const canvas = this.$refs.canvasRef;
    this.imgContext = canvas.getContext("2d");
    this.imgContext.drawImage(this.$refs.imgRef, 0, 0);
  },
  created() {
    this.getHairdresser();
  },
  beforeRouteEnter(to, from, next) {
    if (from.name === "home") {
      next();
      return;
    }
    next({ name: "home" });
  },
};
</script>

