<template>
  <div class="home">
    <Loading v-if="isLoading" />
    <div v-else>
      <div class="button-wrapper">
        <Button
          @click="onOnlineReserve"
          :buttonText="$t('home.retrieveNumber')"
          buttonLarge
          width="250px"
        />
        <Button
          class="btn-search"
          width="250px"
          @click="onNumberSearch"
          :buttonText="$t('home.searchNumber')"
          buttonLarge
        />
      </div>
      <div class="counter-info">
        <!-- TODO: temp remove -->
        <!-- <i18n path="home.currentNumber" tag="div" class="text">
          <template v-slot:number>
            <span class="counter">{{ currentNumber }}</span>
          </template>
        </i18n> -->
        <i18n path="home.waitingNumber" tag="div" class="text">
          <template v-slot:number>
            <span class="counter">{{ waitingNumber }}</span>
          </template>
        </i18n>
      </div>
    </div>
  </div>
</template>

<script>
import Button from "@/components/common/Button";
import { CUSTOMER_STATUS } from "@/const";
import { db } from "@/firebase";
import { todayDate } from "@/utils";
// import { connectPrinter } from "@/printUtils";

export default {
  name: "Home",
  components: {
    Button,
  },
  data() {
    return {
      currentNumber: 0,
      isLoading: false,
      waitingNumber: 0,
    };
  },
  methods: {
    onOnlineReserve() {
      this.$router.push({ name: "onlineReservation" });
    },
    onNumberSearch() {
      this.$router.push({ name: "search" });
    },
  },
  created() {
    const customerRef = db.collection("customer").doc(todayDate);
    // keeps track of data in real time
    // get number of people waiting
    customerRef.onSnapshot((doc) => {
      this.waitingNumber = Object.values(doc.data().customers).filter(
        (customer) => {
          return customer.status === CUSTOMER_STATUS.WAITING;
        }
      ).length;
      customerRef.update({
        waiting: this.waitingNumber,
      });
    });
  },
};
</script>

