import { db } from "@/firebase";
import { todayDate } from "@/utils";

const customerRef = db.collection("customer").doc(todayDate);
const rolesRef = db.collection("roles");

const getLatestNumber = () => {
  return customerRef.get().then((doc) => {
    return doc.data().latestNumber;
  });
};

const getIsAdmin = (uid) => {
  return rolesRef
    .doc(uid)
    .get()
    .then((doc) => {
      return doc.data().isAdmin;
    });
};

export { getLatestNumber, getIsAdmin };
