export const TYPES = {
  SET_USER: "setUser",
  SET_ERROR: "setError",
  SET_ROLE: "setRole",
};

const mutations = {
  [TYPES.SET_USER](state, payload) {
    state.user = payload;
  },

  [TYPES.SET_ERROR](state, payload) {
    state.error = payload;
  },

  [TYPES.SET_ROLE](state, payload) {
    state.role = payload;
  },
};

export default mutations;
