<template>
  <div class="hairdresser">
    <component
      v-if="!isLoading"
      :is="component[pageName]"
      :setPageName="setPageName"
      :nextCustomerData="waitingCustomers[0] || null"
    />
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
import Button from "@/components/common/Button";
import HairdresserNext from "./HairdresserNext";
import HairdresserStart from "./HairdresserStart";
import HairdresserPayment from "./HairdresserPayment";
import { PAGE_NAME } from "./const";
import { db } from "@/firebase";
import { HAIRDRESSER_STATUS, CUSTOMER_STATUS } from "@/const";
import { todayDate } from "@/utils";

export default {
  name: "Hairdresser",
  components: {
    Button,
    HairdresserNext,
    HairdresserStart,
  },
  data() {
    return {
      pageName: PAGE_NAME.NEXT,
      waitingCustomers: [],
      isLoading: false,
      unsubscribe: null,
    };
  },
  computed: {
    ...mapState(["user"]),
    ...mapGetters(["isUserAuth", "isAdminAuth"]),
    component() {
      return {
        [PAGE_NAME.NEXT]: HairdresserNext,
        [PAGE_NAME.START]: HairdresserStart,
        [PAGE_NAME.PAYMENT]: HairdresserPayment,
      };
    },
  },
  methods: {
    // get all the customers waiting in the queue in real time
    getWaitingCustomers() {
      this.isLoading = true;
      const customerRef = db.collection("customer").doc(todayDate);
      this.unsubscribe = customerRef.onSnapshot((doc) => {
        const customers = Object.values(doc.data().customers);
        this.waitingCustomers = customers.filter((customer) => {
          return (
            (customer.status === CUSTOMER_STATUS.WAITING &&
              customer.assignedHairdresser === null) ||
            (customer.assignedHairdresser?.id === this.user.uid &&
              customer.status === CUSTOMER_STATUS.WAITING)
          );
        });
        this.isLoading = false;
      });
    },
    setPageName(pageName) {
      this.pageName = pageName;
    },
    async checkStatus() {
      const hairdresser = await db
        .collection("hairdresser")
        .doc(this.user.uid)
        .get();
      // set page name according to hairdresser status
      if (hairdresser.data().status === HAIRDRESSER_STATUS.WAITING) {
        this.setPageName(PAGE_NAME.NEXT);
      } else if (hairdresser.data().status === HAIRDRESSER_STATUS.CUTTING) {
        this.setPageName(PAGE_NAME.START);
      } else {
        this.setPageName(PAGE_NAME.PAYMENT);
      }
    },
  },
  created() {
    if (!this.isUserAuth || (this.isUserAuth && this.isAdminAuth)) {
      this.$router.replace({
        name: "home",
      });
      return;
    }
    this.checkStatus();
    this.getWaitingCustomers();
  },
  beforeDestroy() {
    this.unsubscribe && this.unsubscribe();
  },
};
</script>

<style lang="scss" scoped>
.hairdresser {
  display: flex;
  align-items: center;
  flex-direction: column;
}
</style>
