/* eslint-disable */
import { formatDate } from "@/utils";
let printer = null;
let ePosDev = new window.epson.ePOSDevice();
let printerStatus = null;

const cbConnect = (data) => {
  printerStatus = data;
  if (data === "OK" || data === "SSL_CONNECT_OK") {
    alert("印表機連線成功！");
    ePosDev.createDevice("local_printer", ePosDev.DEVICE_TYPE_PRINTER, { crypto: false, buffer: true }, cbCreateDevice_printer);
  }
  if (data === "ERROR_TIMEOUT") {
    setTimeout(() => {
      connectPrinter();
    }, 1000);
  }
};

const connectPrinter = (ip = "10.13.237.100", port = 8043) => {
  if (ePosDev === null) {
    ePosDev = new window.epson.ePOSDevice();
  }

  ePosDev.ondisconnect = function () {
    printer = null;
  };

  ePosDev.connect(ip, port, cbConnect);
};

const cbCreateDevice_printer = (devObj, retCode) => {
  if (retCode === "OK") {
    printer = devObj;
    printer.timeout = 60000;
    printer.onreceive = function () {
      // alert("onreceive", res.success);
    };
    printer.oncoveropen = function () {
      // alert("coveropen");
    };
  }
  // create printer failed
};

function print(data, imgContext) {
  printer.addTextLang("zh-tw");
  printer.addTextSmooth(true);
  // logo
  printer.addTextAlign(printer.ALIGN_CENTER);
  printer.brightness = 1.0;
  printer.halftone = printer.HALFTONE_DITHER;
  printer.addImage(imgContext, 0, 0, 272, 177, printer.COLOR_1, printer.MODE_GRAY16);
  // welcome message
  printer.addFeed();
  printer.addTextSize(1, 1);
  printer.addText("Welcome to EZ Cut\n");
  // date
  printer.addTextSize(1, 1);
  printer.addText(`${formatDate(data.createdAt)}\n`);
  // number
  printer.addFeed();
  printer.addFeed();
  printer.addTextSize(2, 2);
  printer.addText(`號碼：${data.number}\n`);
  // qr code
  // printer.addFeed();
  // printer.addFeed();
  // printer.addTextSize(1, 1);
  // printer.addText(`查看目前服務號碼請掃 QR Code \n`);
  // printer.addSymbol("https://ezcut.github.io/ezcut-app/#/", printer.SYMBOL_QRCODE_MODEL_2, printer.LEVEL_DEFAULT, 8, 0, 0);
  printer.addFeed();
  printer.addFeed();
  // cut
  printer.addCut(printer.CUT_FEED);
  // print
  printer.send();
}

const onPrint = (data, imgContext) => {
  return new Promise((resolve, reject) => {
    if (printer) {
      print(data, imgContext);
      resolve();
    } else {
      connectPrinter();
      reject("Not connect to printer, trying to reconnect...");
    }
  });
};

export { onPrint, connectPrinter, printerStatus };
