import { firebase } from "@/firebase";
import store from "@/store";
import { TYPES } from "./mutations";
import { getIsAdmin } from "@/firebaseFunctions";

const actions = {
  loginAction({ commit }, payload) {
    return new Promise((resolve, reject) => {
      firebase
        .auth()
        .signInWithEmailAndPassword(payload.email, payload.password)
        .then((user) => {
          commit(TYPES.SET_USER, user.user);
          getIsAdmin(user.user.uid)
            .then((isAdmin) => {
              if (isAdmin) {
                store.commit(TYPES.SET_ROLE, "Admin");
              }
            })
            .finally(() => {
              resolve();
            });
        })
        .catch((error) => {
          commit(TYPES.SET_ERROR, error.message);
          reject(error);
        });
    });
  },
  setUserAction({ commit }, payload) {
    commit(TYPES.SET_USER, payload);
  },
  logoutAction({ commit }) {
    return new Promise((resolve, reject) => {
      firebase
        .auth()
        .signOut()
        .then(() => {
          commit(TYPES.SET_USER, null);
          commit(TYPES.SET_ROLE, null);
          resolve();
        })
        .catch((error) => {
          commit(TYPES.SET_ERROR, error.message);
          reject(error);
        });
    });
  },
};

export default actions;
