<template>
  <ul class="language-switcher">
    <li
      :class="{ selected: language.value === selectedLang }"
      v-for="language in languages"
      :key="language.value"
      @click="onLanguageSelect(language.value)"
    >
      {{ language.displayName }}
    </li>
  </ul>
</template>

<script>
const languages = [
  {
    value: "zh",
    displayName: "中文",
  },
  {
    value: "en",
    displayName: "EN",
  },
];
export default {
  name: "LanguageSwitcher",
  data() {
    return {
      languages,
    };
  },
  computed: {
    selectedLang() {
      return this.$i18n.locale;
    },
  },
  methods: {
    onLanguageSelect(lang) {
      this.$i18n.locale = lang;
    },
  },
};
</script>

<style lang="scss" scoped>
.language-switcher {
  display: flex;
  color: $white-o-7;

  li {
    padding: 0 8px;
    cursor: pointer;

    &:not(:last-child) {
      border-right: 1px solid $white-o-7;
    }

    &:hover {
      color: $blue;
    }

    &.selected {
      color: $blue;
    }
  }
}
</style>
