<template>
  <div class="input-container">
    <div class="label" v-if="label">{{ label }}</div>
    <input
      class="input"
      :value="value"
      :type="type"
      :placeholder="placeholder"
      :min="min"
      @input="$emit('input', $event.target.value)"
      :inputmode="inputmode"
    />
  </div>
</template>

<script>
export default {
  name: "Input",
  props: {
    label: {
      type: String,
      default: "",
    },
    type: {
      type: String,
      default: "text",
    },
    value: {
      type: [String, Number],
    },
    placeholder: {
      type: String,
      default: "",
    },
    min: {
      type: String,
      default: "1",
    },
    inputmode: {
      type: String,
      default: "text",
    },
  },
};
</script>

