<template>
  <div class="login">
    <Input label="Email" placeholder="Enter Email" v-model="form.email" />
    <Input
      class="input-password"
      label="Password"
      type="password"
      placeholder="Enter Password"
      v-model="form.password"
    />
    <Button @click="onLogin" buttonText="Login">Login</Button>
  </div>
</template>

<script>
import Input from "./common/Input";
import Button from "./common/Button";
import { mapActions, mapState } from "vuex";

export default {
  name: "Login",
  components: {
    Input,
    Button,
  },
  data() {
    return {
      form: {
        email: "",
        passowrd: "",
      },
    };
  },
  computed: {
    ...mapState(["role"]),
  },
  methods: {
    ...mapActions(["loginAction", "setUserAction"]),
    onLogin() {
      this.loginAction(this.form).then(() => {
        if (this.role === "Admin") {
          this.$router.replace({ name: "home" });
        } else {
          this.$router.replace({ name: "hairdresser" });
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.login {
  max-width: 600px;
  margin: 0 auto;

  .input-password {
    margin-top: 24px;
    margin-bottom: 40px;
  }
}
</style>
